@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
.filter {
    width  : 232px;
    padding: 20px;

    border-radius: 5px;
    background   : #fff;
    box-shadow   : 0 2px 8px rgba(0, 0, 0, .1);
}

.filter__title {
    font-size  : 12px;
    font-weight: 600;
    line-height: 12px;

    margin-bottom: 10px;

    text-transform: uppercase;

    color: #4a4a4a;
}

.filter__label {
    font-size: 13px;

    display: flex;

    margin : 0 -20px;
    padding: 10px 20px;

    cursor    : pointer;
    transition: background-color .3s linear;

    align-items: center;
}

.filter__label:hover {
    background: #f1fcff;
}

.filter__checkbox {
    position: relative;

    width       : 20px;
    height      : 20px;
    margin-right: 10px;

    cursor: pointer;
}

.filter__checkbox::before {
    position: absolute;
    top     : 0;
    left    : 0;

    width : 100%;
    height: 100%;

    content: '';

    border          : 1px solid #9abbce;
    border-radius   : 2px;
    background-color: #fff;
}

.filter__checkbox::after {
    position: absolute;
    top     : 50%;
    left    : 50%;

    visibility: hidden;

    width : 65%;
    height: 65%;

    content  : '';
    transform: translate(-50%, -50%);

    border-radius      : 2px;
    background-image   : url(/aviasales-test-task/static/media/Shape.b3ce3c21.svg);
    background-repeat  : no-repeat;
    background-position: center;
    background-size    : contain;
}

.filter__checkbox:checked::after {
    visibility: visible;
}
.logo {
    display: flex;
    justify-content: center;

    width: 60px;
    height: 60px;
    border-radius: 50%;

    box-shadow: 0px 8px 11px rgba(55, 104, 142, 0.25);
}

.logo__img {
    width: 100%;
    height: 100%;
       
    object-fit: cover;
    object-position: center;

    transform: scale(1.06);
}
.ticket {
    background   : #FFFFFF;
    box-shadow   : 0px 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    margin-bottom: 20px;
    padding      : 25px 20px 20px;
}

.ticket__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.ticket__row--mb20 {
    margin-bottom: 20px;
}

.ticket__row--mb10 {
    margin-bottom: 10px;
}

.ticket__col {
    width: 33.3%
}

.ticket__price {
    font-weight: 600;
    font-size  : 24px;
    color      : #2196F3;
}

.ticket__logo {
    width: 110px;
}

.ticket__title {
    font-weight   : 600;
    font-size     : 12px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color         : #A0B0B9;
    line-height: 18px;
}

.ticket__text {
    font-weight: 600;
    font-size  : 14px;
    line-height: 21px;
}
.tickets {
    width: 100%;
}
.tabs {
    display: flex;

    width : 100%;
    height: 50px;
}

.tabs__btn {
    font-family: inherit;
    font-size  : 12px;
    font-weight: 600;
    line-height: 20px;

    width : 50%;
    height: 100%;

    cursor             : pointer;
    transition-duration: .2s;
    transition-property: background-color, border-color, color;
    letter-spacing     : .5px;
    text-transform     : uppercase;

    color           : #4a4a4a;
    border          : 1px solid #dfe5ec;
    outline         : none;
    background-color: #fff;
}

.tabs__btn._active {
    color           : #fff;
    border-color    : #2196f3;
    background-color: #2196f3;
}

.tabs__btn:first-child {
    border-top-left-radius   : 5px;
    border-bottom-left-radius: 5px;
}

.tabs__btn:last-child {
    border-top-right-radius   : 5px;
    border-bottom-right-radius: 5px;
}
*,
::before,
::after {
    box-sizing: border-box;
    margin    : 0;
    padding   : 0;
}

body {
    background-color: #f3f7fa;
    font-family: 'Open Sans', sans-serif;
    color: #4A4A4A;
    font-style : normal;
    font-weight: normal;
}

.app {
    width: 754px;
    margin: 0 auto;
}

.app__logo {
    display: flex;
    justify-content: center;
    margin: 50px 0;
}

.app__row {
    display: flex;
    align-items: flex-start;
}

.app__filter {
    margin-right: 20px;
}

.app__tabs {
    margin-bottom: 20px;
}

.app__container {
    width: 502px
}

.app__tickets {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Preloader {
    width        : 50px;
    height       : 50px;
    border-radius: 50%;
    border       : 6px solid rgba(33, 150, 243, 0.5);
    border-top   : 6px solid rgba(33, 150, 243, 1);
    animation    : preloader-rotate 1s linear 0s infinite;
}

@keyframes preloader-rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}
